.form {
  padding: 20px;
  display: flex;
  flex-direction: column;
  width: 200px;
  justify-content: center;
  align-items: baseline;
}
.label {
  margin-bottom: 20px;
  color: green;
  font-size: 22px;
}
.input {
  width: 150px;
  height: 30px;
  padding: 4px;
  border-radius: 5px;
  border-color: greenyellow;
}
.button {
  background-color: orangered;
  border-color: transparent;
  color: blanchedalmond;
  width: 100%;
  height: 35px;
  border-radius: 20px;
}
